export default function Experience(props) {
  const { item } = props;

  return (
    <div className="entry-card">
      <h4>{item.title}</h4>
      <h5>{item.company}</h5>
      <p className="date-range">{item.date}</p>
      <ul>
        {item.responsibilities.map((resp, index) => (
          <li key={index}>{resp}</li>
        ))}
      </ul>
    </div>
  );
}